export default {
  data(){
    return{
      isLogin:false,//
      todayYearDate:"",
      nowDay:"",//
      nowMonth:"",//
      nowYear:"",//
      nowWeek:"",//
      name:"",//
      nowMonthIncome:"0.0",//
      nowMonthPay:"0.0",//
      budgetBalance:"0.0",//
      isRecordText:"",//
      nowDayIncome:"0.0",//
      nowDayPay:"0.0",//
      weekRegion:"",//
      weekStart:"",
      weekEnd:"",
      nowWeekIncome:"0.0",//
      nowWeekPay:"0.0",//
      monthRegion:"",//
      monthStart:"",
      monthEnd:"",
      todayIconSrc:"",  //
      monthIconSrc:"",//
      weekIconSrc:"", //
      //
      toastShow:false,
      toastType:"warn",
      toastText:"",
    }
  },
  methods:{
    list(){
      if(this.isLogin){
        this.$router.push('/record');
      }else{
        this.toastText="";
        this.toastType="warn";
        this.toastShow=true;
      }
    },
    login(){
     this.$router.push("/login");
    },
    personal(){
      if(this.isLogin){
        this.$router.push('/personal');
      }else{
        this.toastText="";
        this.toastType="warn";
        this.toastShow=true;
      }

    },
    detail(){
      if(this.isLogin){
        this.$router.push('/detail');
      }else{
        this.toastText="";
        this.toastType="warn";
        this.toastShow=true;
      }

    },
    money(){
      this.$router.push('/money');
    },
    sociation(){
      this.$router.push('/sociation');
    },
    //
    isNowMonthIncome(){
      if(this.isLogin){
        //
        this.$store.commit("saveCheckOptions",{
          checkStartDate:this.nowYear+"-"+this.monthStart,
          checkEndDate:this.nowYear+"-"+this.monthEnd,
          checkMember:this.name,
          checkItem:"",
          checkItemDetail:"",
        });
        this.$store.commit("changeDetailPage","home")
        this.$router.push('/checkdetails');
      }else{
        this.toastText="";
        this.toastType="warn";
        this.toastShow=true;
      }
    },
    isNowMonthPay(){
      if(this.isLogin){
        //
        this.$store.commit("saveCheckOptions",{
          checkStartDate:this.nowYear+"-"+this.monthStart,
          checkEndDate:this.nowYear+"-"+this.monthEnd,
          checkMember:this.name,
          checkItem:"",
          checkItemDetail:"",
        });
        this.$store.commit("changeDetailPage","home")
        this.$router.push('/checkdetails');
      }else{
        this.toastText="";
        this.toastType="warn";
        this.toastShow=true;
      }
    },
    isBudget(){
      if(this.isLogin){
        this.$router.push('/budget');
      }else{
        this.toastText="";
        this.toastType="warn";
        this.toastShow=true;
      }
    },
    isNowDay(){
      if(this.isLogin){
        //
        this.$store.commit("saveCheckOptions",{
          checkStartDate:this.todayYearDate,
          checkEndDate:this.todayYearDate,
          checkMember:this.name,
          checkItem:"",
          checkItemDetail:"",
        });
        this.$store.commit("changeDetailPage","home")
        this.$router.push('/checkdetails');
      }else{
        this.toastText="";
