
 import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

//
var foods=[ '', '', '', ''];
var traffics=['','','','','','','','','','','','','','',''];
var shops=['','','','','','','','','','','','','',''];
var plays=['ktv','','','','','','','','','','','',''];
var medical_educations=['','','','','','','','','','','','',''];
var families=['','','','','','','','','','','','',''
  ,'','',''];
var invests=['','','','','','','','','','P2P','','','',''];
var human_feelings=['','','','','','','','',''];
var business=['','','','','','','','','','','',''];
var pay_details_index=[foods,traffics,shops,plays,medical_educations,families,invests,human_feelings,business,['']];

export default{
  data(){
    return{
      payMenu:[{text:''}, {text:''}, {text:''}, {text:''}, {text:''}, {text:''}, {text:''}, {text:''}, {text:''}//
      ],
      payDetails:foods//
    }
  },
  methods:{
    back(){
      if(this.$store.state.page=="record"){
        this.$router.push('/record');
      }
      if(this.$store.state.page=="detail"){
        this.$router.push('/detail');
      }

     // this.$store.commit('changePayPage');
    },
    openDetails($index){
      var pay_menus=document.getElementById('left').getElementsByTagName('p');
      for(var i=0;i<9;i++)
      {
        if(i!==$index)
        {
          pay_menus[i].setAttribute('class','pay-menu');
        }
      }
      pay_menus[$index].setAttribute('class','pay-menu selected');
      this.payDetails=pay_details_index[$index];
      var pay_details=document.getElementById('right').getElementsByTagName('p');
      for(var i=0;i<pay_details.length;i++)
      {
        pay_details[i].setAttribute('class','pay-menu');
      }
     // this.$store.commit('changeSelectedItem',pay_menus[$index]);
      },
    selectDetails($index){
      var pay_details=document.getElementById('right').getElementsByTagName('p');
       /*for(var i=0;i<pay_details.length;i++)
      {
        if(i!=$index)
        {
          pay_details[i].setAttribute('class','pay-menu');
        }
      }
      pay_details[$index].setAttribute('class','pay-menu details_selected');*/
      if(this.$store.state.page=="record"){
        this.$store.commit('changePaySelected',pay_details[$index].textContent);
        this.$router.push('/record');
      }
      if(this.$store.state.page=="detail"){
        this.$store.commit('changeDetailSelected',pay_details[$index].textContent);
        this.$router.push('/detail');
      }

    }
  },
  mounted:function () {
    console.log("555");
    if(this.$store.state.page=="record"){//
      this.payMenu=[{text:''}, {text:''}, {text:''}, {text:''},
        {text:''}, {text:''}, {text:''}, {text:''}, {text:''}
      ];
      if(foods.length>4){//
        foods.splice(0,6);traffics.splice(0,1);shops.splice(0,1);
        plays.splice(0,1);medical_educations.splice(0,1);families.splice(0,2);
        invests.splice(0,1);human_feelings.splice(0,1);business.splice(0,1);
      }
    }
    if(this.$store.state.page=="detail"){
      console.log(foods);
      this.payMenu=[{text:''}, {text:''}, {text:''}, {text:''}, {text:''},
        {text:''}, {text:''}, {text:''}, {text:''}, {text:''}//
      ];
      if(foods.length==4){//
        foods.unshift('','','','','','');traffics.unshift('');
        shops.unshift('');plays.unshift('');
        medical_educations.unshift('');families.unshift('','');
        invests.unshift('');human_feelings.unshift('');
        business.unshift('');
      }
    }

    var pay_menus=document.getElementById('left').getElementsByTagName('p');
    pay_menus[0].setAttribute('class','pay-menu selected');

    var _this=this;
    plus.key.addEventListener("backbutton",function(){
      _this.back();
    });
  },
  components:{
    XHeader,
    Flexbox,
    FlexboxItem,
    XButton,
    Scroller
  }
}





