
  import hex_md5 from '../assets/js/md5.js'
  import XHeader from 'vux/src/components/x-header/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popover from 'vux/src/components/popover/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default{
  data(){
    return{
      newPSD:"",
      againPSD:"",
      //
      toastShow:false,
      toastText:"",
      textShow:false,
      text:"",
      cellPhone:""
    }
  },
  methods:{
    back(){
      this.$router.push("/lookPSD");
    },
    //
    confirm(){
      var _this=this;
      //
      if(this.newPSD==""){
        this.toastShow=true;
        this.toastText=""
      }else if(this.againPSD==""){
        this.toastShow=true;
        this.toastText=""
      }else{
        //(6-16,)
        var psd = /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[~!@#$%^&*()_+`\-={}:";'<>?,.\/]).{6,16}$/;
        var re = new RegExp(psd);
        if(re.test(this.newPSD)){
          //
          if(this.newPSD==this.againPSD){
            //
            var md5Psd=hex_md5(this.newPSD);
            //
            console.log(_this.cellPhone);
            this.$instance.post(
              'user/setNewPSD',{
                phone:_this.cellPhone,
                password:md5Psd
              })
              .then(function(response){
                console.log(response);
                console.log(response.data);
                if(response.status==200){
                  var res=response.data;
                  if(res.code==200){
                    _this.textShow=true;
                    _this.text=""
                    //
                    setTimeout(function(){
                      _this.$router.push("/login");
                    },3000);
                  } else{
                    _this.toastShow=true;
                    _this.toastText=res.message+""
                  }
                }
              })
              .catch(function(err){
                console.log(err);
              });
          }else{
            this.toastShow=true;
            this.toastText=""
          }
        }else{
          this.toastShow=true;
          this.toastText=""
        }
      }
    }
  },
  components:{
    XHeader,
    Box,
    XButton,
    XInput,
    Group,
    Popover,
    Toast
  },
  mounted: function () {
    this.cellPhone=this.$store.state.cellPhone;
    /*  */
    var set_PSD=document.getElementById('set_PSD').getElementsByTagName('input');
     set_PSD[0].focus();
    var _this=this;
    plus.key.addEventListener("backbutton",function(){
      _this.back();
    });
  }
}




