
import XHeader from 'vux/src/components/x-header/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default{
  data(){
    return{
      isPay:true,//
      money:"0.0",//
      remark:"",//
      showDate:false,//
      selectedDate:"",//
      paySelectedItem:"...",//
      incomeSelectedItem:"...",//
      member:"",//
      name:"",//
      type:"",//
      recordReason:"",//

      //
      toastShow:false,
      toastType:"warn",
      toastText:"",
    }
  },
  methods:{
    back(){
     this.$store.commit("changeRecordState",true)
      this.$router.push('/home');
    },
    //
     payInit(){
       var  btns=document.getElementById("pay-btns").getElementsByTagName("button")
       for(var i=0;i<btns.length;i++){
         btns[i].className="weui-btn weui-btn_mini weui-btn_default weui-btn_plain-default ";
       }
       btns[0].className="weui-btn select-btn weui-btn_mini weui-btn_default weui-btn_plain-default";
       this.recordReason=""
       var divs=document.getElementById("pay-box").getElementsByTagName("div");
       for(var i=1;i<divs.length;i++){
         divs[i].className="little-container";
       }
       this.member=""
     },
    //
    incomeInit(){
      var  btns=document.getElementById("income-btns").getElementsByTagName("button")
      for(var i=0;i<btns.length;i++){
        btns[i].className="weui-btn weui-btn_mini weui-btn_default weui-btn_plain-default ";
      }
      btns[0].className="weui-btn select-btn weui-btn_mini weui-btn_default weui-btn_plain-default";
      this.recordReason=""
      this.member=this.name
    },
    payClick(){
      this.isPay=true;
      this.$store.commit("changeRecordState",true);
      this.payInit();
    },
    incomeClick(){
      this.isPay=false;
      this.$store.commit("changeRecordState",false);
     this.incomeInit();

    },
    payMore(){
      this.$store.commit("saveRecordData",{
        money:this.money,
        remark:this.remark,
        selectedDate:this.selectedDate
      });
      this.$store.commit("changePage","record");
      this.$router.push('/paymore')
    },
    incomeMore(){
      this.$store.commit("saveRecordData",{
        money:this.money,
        remark:this.remark,
        selectedDate:this.selectedDate
      });
      this.$store.commit("changePage","record");
      this.$router.push('/incomemore')
    },
    selected(e){
      var btns;
      if(this.isPay){
        btns=document.getElementById("pay-btns").getElementsByTagName("button")
      }else{
        btns=document.getElementById("income-btns").getElementsByTagName("button")
      }
      for(var i=0;i<btns.length;i++){
        btns[i].className="weui-btn weui-btn_mini weui-btn_default weui-btn_plain-default ";
      }
     var self=document.getElementById(e.target.id);
      self.className="weui-btn select-btn weui-btn_mini weui-btn_default weui-btn_plain-default ";

      switch(e.target.id){
        case "fruit":{this.recordReason="";break;}
        case "vege":{this.recordReason="";break;}
        case "water":{this.recordReason="";break;}
        case "morning":{this.recordReason="";break;}
        case "lunch":{this.recordReason="";break;}
        case "dinner":{this.recordReason="";break;}
        case "pay-more":{this.recordReason=this.paySelectedItem;break;}
        case "mon-income":{this.recordReason="";break;}
        case "two-job":{this.recordReason="";break;}
        case "welfare":{this.recordReason="";break;}
        case "gift":{this.recordReason="";break;}
        case "red-bag":{this.recordReason="";break;}
        case "award":{this.recordReason="";break;}
        case "income-more":{this.recordReason=this.incomeSelectedItem;break;}
      }
    },
    selectedMember(e){
      if(this.isPay)
      {
        var divs=document.getElementById("pay-box").getElementsByTagName("div");
        for(var i=0;i<divs.length;i++){
          divs[i].className="little-container";
        }
        var member=document.getElementById(e.target.id);
        member.parentNode.className="little-container select";
        this.member=member.nextSibling.nextSibling.innerHTML;
      }else{
        this.member=this.name
      }

    },
    selectDate(){
      if(this.showDate){
        this.showDate=false;
      }else{
        this.showDate=true;
      }
    },
    record(){
      var _this=this;
      if(this.isPay){
        this.type="";
        console.log("")
      }else{
        this.type="";
        console.log("")
      }
     var num =/^(([1-9]{1}\d*)|([0]{1}))(\.(\d){1,1})?$/;
      var re = new RegExp(num);
      if(this.money==""){//this.money
        this.toastShow=true;
        this.toastType="warn";
        this.toastText="";
      }else if(this.money<=0){
        this.toastShow=true;
        this.toastType="warn";
        this.toastText="0";
      }else if(re.test(this.money)){
        //
        this.$instance.post('bill/addBill',{
          num:_this.money,
          member:_this.member,
          typeName:_this.recordReason,
          remark:_this.remark,
          time:_this.selectedDate,
          type:_this.type,
        }).then(function(response){
          if(response.status==200){
            var res=response.data;
            if(res.code===200){
              console.log("sss");
              _this.toastShow=true;
              _this.toastType="default";
              _this.toastText="";
              _this.money="0.0";
            }else if(res.code===404){
            }
          }
        })
          .catch(function(err){
            console.log(err);
          });
      }else {
        this.toastShow=true;
        this.toastType="warn";
        this.toastText="";
      }

    }
  },
  components:{
    XHeader,
    ButtonTab,
    ButtonTabItem,
    Icon,
    Group,
    XInput,
    Box,
    XButton,
    Cell,
    InlineCalendar,
    Toast
  },
  mounted:function () {
    //
    this.paySelectedItem=this.$store.state.paySelectedItem;//
    this.incomeSelectedItem=this.$store.state.incomeSelectedItem;//
    this.isPay=this.$store.state.isPay;//or
    this.money=this.$store.state.money;//
    this.remark=this.$store.state.remark;//
    //
    if(this.$store.state.selectedDate==""){//
      this.selectedDate=this.$store.state.todayDate;
    }else{
      this.selectedDate=this.$store.state.selectedDate;
    }
    //
    if(this.isPay){
      this.payInit();
    }else{
      this.incomeInit();
    }

    //
    var _this=this;
    this.$instance.get('user/getUser').then(function(response){
      if(response.status==200){
        var res=response.data;
        if(res.code===200){
          console.log("sss");
          document.getElementsByTagName("img")[1].src=res.user.icon;
          document.getElementsByTagName("img")[2].src=res.user.icon;
          _this.name=res.user.username;
        }else if(res.code===400){
          console.log(res.message);
        }
      }
    })
      .catch(function(err){
        console.log(err);
      });
    plus.key.removeEventListener("backbutton",function(){
      console.log("remove");
    });
    plus.key.addEventListener("backbutton",function(){
      _this.back();
    });
  }
}



