
// import Chart from 'chart.js/src/chart.js';
  import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

  export default{
    data(){
      return{
        isDelete:false,//
        details:[//time  content   text member   ididdi isShow
          {time:"2017-05-21", contents:[{text:"20",member:"", id:"21",show:true}, {text:"2000",member:"", id:"2f",show:false},{text:"2000",member:"", id:"2f",show:false},{text:"2000",member:"", id:"2f",show:false},{text:"2000",member:"", id:"2f",show:false},
            {text:"2000",member:"", id:"2f",show:false}]},
          {time:"2017-05-21", contents:[{text:"20",member:"", id:"21",show:true}, {text:"2000",member:"", id:"2f",show:false},{text:"2000",member:"", id:"2f",show:false},{text:"2000",member:"", id:"2f",show:false},{text:"2000",member:"", id:"2f",show:false},
            {text:"2000",member:"", id:"2f",show:false}]},
          {time:"2017-05-21", contents:[{text:"20",member:"", id:"21",show:true}, {text:"2000",member:"", id:"2f",show:false},{text:"2000",member:"", id:"2f",show:false},{text:"2000",member:"", id:"2f",show:false},{text:"2000",member:"", id:"2f",show:false},
            {text:"2000",member:"", id:"2f",show:false}]},
          {time:"2017-05-21", contents:[{text:"20",member:"", id:"21",show:true}, {text:"2000",member:"", id:"2f",show:false},{text:"2000",member:"", id:"2f",show:false},{text:"2000",member:"", id:"2f",show:false},{text:"2000",member:"", id:"2f",show:false},
            {text:"2000",member:"", id:"2f",show:false}]},
          {time:"2017-05-26", contents:[ {text:"",member:"", id:"",show:false}]},
        ],
        deleteId:"",
      }
    },
    methods:{
      back(){
        this.$router.push("/checkdetails");
      },
      deleteItem(e){
        this.isDelete=true;
        this.deleteId=e.target.id;
        console.log(e.target.id);
      },
      onCancel(){
        this.isDelete=false;
      },
      onConfirm(){
        var _this=this;
        this.$instance.post('bill/deleteBill ',{
         id:_this.deleteId
        }).then(function(response){
          if(response.status==200){
            console.log(response);
            var res=response.data;
            console.log(res);
            if(res.code===200){
              console.log("");
              _this.isDelete=false;
              _this.reload();
            }else if(res.code===404){}
          }
        })
          .catch(function(err){
            console.log(err);
          });
      },
      openDetails(e){
        var div=e.target.parentNode.parentNode.parentNode.parentNode.parentNode.nextSibling.nextSibling;
        if(div.style.display=="block"){
          div.style.display="none"
        }else{
          div.style.display="block"
        }
      },
      reload(){
        var _this=this;
        //
        var startDate=this.$store.state.checkStartDate;
        var endDate=this.$store.state.checkEndDate;
        var member=this.$store.state.checkMember;
        var item=this.$store.state.checkItem;
        var itemDetail=this.$store.state.checkItemDetail;
        console.log(startDate);
        console.log(endDate);
        console.log(member);
        console.log(item);
        console.log(itemDetail);
        //
        this.$instance.post('bill/getBills',{
          startDay:startDate,
          endDay:endDate,
          member:member,
          type:item,
          typeName:itemDetail
        }).then(function(response){
          if(response.status==200){
            console.log(response);
            var res=response.data;
            console.log(res);
            if(res.code===200){
              if(res.list.length>0){
                _this.details=res.list;
              }else{
                _this.details=[
                  {time:startDate+""+endDate, contents:[ {text:"",member:"", id:"",show:false}]},
                ];
              }

            }else if(res.code===404){}
          }
        })
          .catch(function(err){
            console.log(err);
          });
      }
    },
    components:{
      XHeader,
      Cell,
      Group,
      Timeline,
      TimelineItem,
      Icon,
      Confirm
    },
    mounted(){
      //
     this.reload();
      var _this=this;
      plus.key.addEventListener("backbutton",function(){
        _this.back();
      });

    }
  }



