
import XHeader from 'vux/src/components/x-header/index.vue'

export default{
  data(){
    return{
      incomeTable:['', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', ''
      ]

    }
  },
  methods:{
    back(){
      if(this.$store.state.page=="record"){
        this.$router.push('/record');
      }
      if(this.$store.state.page=="detail"){
        this.$router.push('/detail');
      }
     // this.$store.commit('changeIncomePage');//record
    },
     selectDetails($index){
       if(this.$store.state.page=="record"){
         this.$store.commit('changeIncomeSelected',this.incomeTable[$index]);//
         this.$router.push('/record');
       }
       if(this.$store.state.page=="detail"){
         this.$store.commit('changeDetailSelected',this.incomeTable[$index]);//
         this.$router.push('/detail');
       }
     }
  },
  components:{
    XHeader
  },
  mounted:function(){
    if(this.$store.state.page=="record"){
      this.incomeTable=['', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', ''//record
      ]
    }
    if(this.$store.state.page=="detail"){//detail
      this.incomeTable=['', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', ''
      ]
    }
    var _this=this;
    plus.key.addEventListener("backbutton",function(){
      _this.back();
    });
  }
}



