
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'

const payItems=['', '', '', '', '', '', '', '', ''];
export default{
  data(){
    return{
      currentDate:"",//
      budgets:"0.0",//
      use:"0.0",//
     unUse:"0.0",//
      detailMoney:"0.0",//
      isSaveShow:false,//
      isShow:false,//
      payItem:[""],//
      payItems:[['', '', '', '', '', '', '', '', '']],//
      payItemText:"",
      //
      budgetDetail:[//0.0
        {name:"",budget: "0.0",percent:0,balance: "0",img:"http://115.159.188.223/budget/food.png"},{name:"",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/traffic.png"},{name:"",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/shop.png"},
        {name:"",budget: "0.0",percent:0,balance: "0",img:"http://115.159.188.223/budget/play.png"}, {name:"",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/hospital.png"},{name:"",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/home.png"},
        {name:"",budget: "0.0",percent:0,balance: "0",img:"http://115.159.188.223/budget/mana-mon.png"},{name:"",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/favor.png"},{name:"",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/business.jpg"},
      ],

      //
      toastText:"",
      toastShow:false,
      toastType:"warn",

      //
      changeItem:"",

    }
  },
  methods:{
    back(){
      this.$router.push("/home");
    },
    selectPay(){
      if(this.isShow){
        this.isShow=false;
      }else{
        this.isShow=true;
      }
      console.log("sss");
    },
    payItemChange(){
      //this.isShow=false;
     this.changeItem=this.payItem[0];
      console.log("ddd");
      this.payItemText=this.payItem[0]+""
    },
    monthChange(){
      var month=new Date(this.currentDate);
      var currentMonth=new Date(this.$store.state.todayDate);
     if(month<currentMonth){
       document.getElementById("setBudget").style.display="none";
       document.getElementById("details").className="budget-detail-hide";
     }else{
       document.getElementById("setBudget").style.display="block";
       document.getElementById("details").className="budget-detail";
     }

      this.reload();
    },
    save(){
      //
      var num =/^(([1-9]{1}\d*)|([0]{1}))(\.(\d){1,1})?$/;
      var re = new RegExp(num);
      var money=this.detailMoney;
      if(money==""){
        this.toastShow=true;
        this.toastType="warn";
        this.toastText="";
      }else if(money<0){
        this.toastShow=true;
        this.toastType="warn";
        this.toastText="0";
      }else if(re.test(money)){
        this.isSaveShow=true;
      }else {
        this.toastShow=true;
        this.toastType="warn";
        this.toastText="";
      }


    },
    onCancel(){
      this.isSaveShow=false;
    },
    onConfirm(){
      var month=new Date(this.currentDate).getMonth()+1;
      var year = new Date(this.currentDate).getYear(); //
      year += (year < 2000) ? 1900 : 0;
      var _this=this;
      //
     this.$instance.post('budget/addBudget',{
        num:_this.detailMoney,
        month:month,
       year:year,
        typeName:_this.payItem[0],
      }).then(function(response){
        if(response.status==200){
          var res=response.data;
          if(res.code===200){
            _this.toastShow=true;
            _this.toastType="default";
            _this.toastText="";
            _this.reload();//
          }else if(res.code===404){}
          _this.toastShow=true;
          _this.toastType="warn";
          _this.toastText=res.message;
        }
      })
        .catch(function(err){
          console.log(err);
        });
      this.isSaveShow=false;//
    },
    //
    reload(){
      var _this=this;
      var month=new Date(this.currentDate).getMonth()+1;
      var year = new Date(this.currentDate).getYear(); //
      year += (year < 2000) ? 1900 : 0;
      this.$instance.post('budget/getBudgets',{  //
        month:month, year:year
      }).then(function(response){
        if(response.status==200){
          var res=response.data;
          if(res.code===200){
            _this.budgets=res.budgets;
            _this.use=res.use;
            _this.unUse=res.unUse;
            if(res.budgetDetails.length>0){
              _this.budgetDetail=res.budgetDetails;//
            }else{
              _this.budgetDetail=[//0.0
                {name:"",budget: "0.0",percent:0,balance: "0",img:"http://115.159.188.223/budget/food.png"},{name:"",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/traffic.png"},{name:"",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/shop.png"},
                {name:"",budget: "0.0",percent:0,balance: "0",img:"http://115.159.188.223/budget/play.png"}, {name:"",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/hospital.png"},{name:"",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/home.png"},
                {name:"",budget: "0.0",percent:0,balance: "0",img:"http://115.159.188.223/budget/mana-mon.png"},{name:"",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/favor.png"},{name:"",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/business.jpg"},
              ];
            }
          }else if(res.code===404){
          }
        }
      })
        .catch(function(err){
          console.log(err);
        });
      this.detailMoney="0.0";

    }
  },
  mounted:function () {
    var _this=this;
    this.currentDate=this.$store.state.todayDate;
    this.reload();
    plus.key.removeEventListener("backbutton",function(){
      console.log("remove");
    });
    plus.key.addEventListener("backbutton",function(){
      _this.back();
    });
  },
  components:{
    XHeader,
    Group,
    Cell,
    Icon,
    XInput,
    InlineCalendar,
    Picker,
    XButton,
    XProgress,
    Confirm,
    Toast,
    Calendar
  }
}



